@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One);
html, body {
  height: 100%;
}

#root {
  height: 100%;
  font-family: 'Julius Sans One', sans-serif;
}
button:focus {
  outline: none !important;
}

.text-ghost {
  opacity: 0.7;
}
.home {
  background: url(/react-photo-gallery/static/media/bg-home.b1c6271b.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  padding-top: 250px;
}

.btn-ghost {
  background: transparent;
}

.btn-ghost.btn-white {
  color: #fff;
  border: 1px solid #fff;
}

.btn-ghost.btn-white:hover {
  color: #3c3c3c;
  background: #fff;
}
img:hover {
  cursor: pointer;
  opacity: 0.5;
  -webkit-transition: 0.7s;
  transition: 0.7s;
}

.gallery-container {
  padding-top: 100px;
} 

.app {
  height: 100%;
}
